<template>
  <span class="comment-time">
    <time
      v-if="!hide"
      :datetime="createTime | formatDate('yyyy-MM-ddTHH:mm:ss')"
      itemprop="datePublished"
      >{{ createTime | prettyDate }}</time
    >
    <span v-else>
      {{ prettyDate(createTime) }}
    </span>
  </span>
</template>

<script>
export default {
  components: {
    // FontAwesomeIcon
  },
  props: {
    createTime: {
      type: Number
    },
    hide: {
      type: Boolean,
      required: false
    }
  },
  data() {
    return {
      isCollapsed: true
    }
  },
  watch: {
    isCollapsed(newValue) {
      if (this.onCollapsed) {
        this.onCollapsed(newValue)
      }
      // if (this.id) {
      //   this.saveCollapsedState(newValue)
      // }
      // this.$emit('collapseChanged', newValue)
    }
  },
  mounted() {
    // this.isCollapsed = true
    if (this.id) {
      // const state = this.getCollapseState()
      // if (state) {
      //   this.isCollapsed = state[this.id]
      // }
    }
  },
  methods: {
    prettyDate(timestamp) {
      const minute = 1000 * 60
      const hour = minute * 60
      const day = hour * 24
      const month = day * 30
      const year = month * 12
      const diffValue = new Date().getTime() - timestamp
      if (diffValue / minute < 1) {
        return '刚刚'
      } else if (diffValue / minute < 60) {
        return parseInt(diffValue / minute) + '分钟前'
      } else if (diffValue / hour <= 24) {
        return parseInt(diffValue / hour) + '小时前'
      } else if (diffValue / day <= 7) {
        // return parseInt(diffValue / day) + '天前'
        return '一周以内'
      } else if (diffValue / day <= 30) {
        return '一个月以内'
        // return filters.formatDate(timestamp, 'yyyy-MM-dd HH:mm:ss')
      } else if (diffValue / day <= 180) {
        return '半年以内'
        // return filters.formatDate(timestamp, 'yyyy-MM-dd HH:mm:ss')
      } else if (diffValue / day <= 360) {
        // return parseInt(diffValue / day) + '天前'
        return '一年以内'
      }
      return '一年以前'
    }
  }
}
</script>
