import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faAngleUp, faAngleDown } from '@fortawesome/free-solid-svg-icons'

import utils from '~/common/utils'
import Panel from '~/components/Panel'
import SectionTime from '~/components/SectionTime'

library.add(faAngleUp, faAngleDown)
export default {
  components: {
    // Panel,
    FontAwesomeIcon,
    SectionTime
  },
  props: {
    topics: {
      type: Array,
      default() {
        return []
      },
      required: false
    },
    showAd: {
      type: Boolean,
      default: false
    },
    openNewPage: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      blank: '',
      isCollapsed: true,
      isCollapsedArray: []
    }
  },
  watch: {
    topics(newValue, oldValue) {
      if (this.topics && this.topics.length !== undefined) {
        this.isCollapsedArray = new Array(this.topics.length).fill(true)
      }
    }
  },
  mounted() {
    if (this.openNewPage) {
      this.blank = '_blank'
    }
    // console.log('topics:' + JSON.stringify(this.topics))
    if (this.topics && this.topics.length > 0) {
      this.isCollapsedArray = new Array(this.topics.length).fill(true)
    }

    // console.log(JSON.stringify(this.isCollapsedArray))
  },
  methods: {
    async clickCollapse(index) {
      if (this.topics[index].content !== undefined) {
      } else {
        try {
          const topic = await this.$axios.get(
            '/api/topic/' + this.topics[index].topicId
          )
          this.topics[index].content = topic.content
          this.topics[index].imageList = topic.imageList
        } catch (e) {}
      }

      this.$set(this.isCollapsedArray, index, !this.isCollapsedArray[index]) // 改变数组

      // console.log('detail:' + JSON.stringify(topic))
    },
    getTopText(topic) {
      const topType = topic.topType ? topic.topType : 0
      return utils.getTopText(topType)
    },
    onCollapseBack(state) {
      // console.log(state)
    },
    async like(topic) {
      this.isCollapsed = !this.isCollapsed
      try {
        await this.$axios.post('/api/topic/like/' + topic.topicId)
        topic.liked = true
        topic.likeCount++
      } catch (e) {
        if (e.errorCode === 1) {
          this.$toast.info('请登录后点赞！！！', {
            action: {
              text: '去登录',
              onClick: (e, toastObject) => {
                utils.toSignin()
              }
            }
          })
        } else {
          this.$toast.error(e.message || e)
        }
      }
    }
  }
}
